.gpt__header {
  display: flex;
}

.gpt__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}

.gpt__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.gpt__header-content p {
  font-family: var(--font-family);
  font-size: 20px;
  line-height: 27px;
  color: var(--color-text);
  margin-top: 1.5rem;
}

.gpt__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
  flex-direction: row;
}

.gpt__header-content__input input {
  flex: 1;
  width: 100%;
  min-height: 50px;
  font-family: var(--font-family);
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
  padding: 0 1rem;
  outline: none;

  font-size: 20px;
  line-height: 27px;
  color: #fff;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.gpt__header-content__input button {
  flex: 0.6;
  width: 100%;
  min-height: 50px;

  background-color: #ff482d;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  /* transform: matrix(-1, 0, 0, 1, 0, 0); */
  font-family: var(--font-family);
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #fff;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  border: none;
}

.gpt__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.gpt__header-content__people img {
  width: 181.79px;
  height: 38px;
}

.gpt__header-content__people p {
  margin-left: 0 0 0 1rem;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 30px;
  color: #fff;
  text-align: center;
}

.gpt__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt__header-image img {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1050px) {
  .gpt__header {
    flex-direction: column;
  }
  .gpt__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 658px) {
  .gpt__header h1 {
    font-size: 48px;
    line-height: 60px;
  }
  .gpt__header p {
    font-size: 16px;
    line-height: 24px;
  }
  .gpt__header-content__people {
    flex-direction: column;
  }
  .gpt__header-content__people p {
    margin: 0;
  }

  .gpt__header-content__input input,
  .gpt__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 458px) {
  .gpt__header h1 {
    font-size: 36px;
    line-height: 48px;
  }
  .gpt__header p {
    font-size: 14px;
    line-height: 24px;
  }
  .gpt__header-content__input input,
  .gpt__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}
